import { NgZone } from '@angular/core';

import { services } from 'zetapush-js';

import { Observable } from 'rxjs/Observable';
import'rxjs/add/operator/publish';

import { ZetaPushClient } from './core';

const /** @type {?} */ toPascalCase = (word = '') => `${word.charAt(0).toUpperCase()}${word.substring(1)}`;

const /** @type {?} */ getExtensionsAndListener = (Class: any, zone: NgZone) => {
  const /** @type {?} */ filter = (element: string) => element !== 'constructor';
  const /** @type {?} */ methods = Object.getOwnPropertyNames(Class.prototype).filter(filter);
  const /** @type {?} */ extensions = {};
  const /** @type {?} */ listener = methods.reduce((reducer, method) => {
    const /** @type {?} */ source = new Observable((observer) => {
      reducer[method] = ({ data }: { data: { errors: Array<any>, result: any } }) => {
        console.log(`Api::on${toPascalCase(method)}`, data);
        zone.run(() => {
          const { errors, result } = data;
          if (errors.length) {
            observer.error(result);
          } else {
            observer.next(result);
          }
        });
      };
    });
    const /** @type {?} */ published = source.publish();
    extensions[`on${toPascalCase(method)}`] = published;
    published.connect();
    return reducer;
  }, {});
  return { extensions , listener };
};

export interface ApiError {
  code: string;
  location: string;
  message: string;
}
export class Api extends services.Macro {
/**
 * @return {?}
 */
$getUserId(): string {
    return '<abstract>';
  }
}
/**
 * @param {?} client
 * @param {?} zone
 * @param {?} Api
 * @return {?}
 */
export function createApi(client: ZetaPushClient, zone: NgZone, Api: any) {
  const { extensions , listener } = getExtensionsAndListener(Api, zone);
  const /** @type {?} */ api = client.createAsyncMacroService({
    Type: Api,
    listener
  });
  const /** @type {?} */ $publish = api.$publish;
  api.$publish = (method: string, parameters: any, hardFail?: boolean, debug?: number) => new Promise<any>((resolve, reject) => {
    console.warn(`Api::${method}`, parameters);
    const /** @type {?} */ onSuccess = (message: any) => zone.run(() => resolve(message));
    const /** @type {?} */ onError = (errors: any) => zone.run(() => reject(errors));
    $publish(method, parameters, hardFail, debug).then(onSuccess, onError);
  });
  return /** @type {?} */(( Object.assign(api, extensions, {
    $getUserId: () => client.getUserId()
  }) as Api));
}
